






import { Component, Vue } from 'vue-property-decorator';
export default class APP extends Vue {
    // 初始化数据
    msg = 123

    // 声明周期钩子
    mounted () {
        this.greet()
    }

    // 计算属性
    get computedMsg () {
        return 'computed ' + this.msg
    }

    // 方法
    greet () {
        alert('greeting: ' + this.msg)
    }
}
